import React, { FunctionComponent } from 'react';
import './style.scss';

interface ICategoryPageHeader {
  title: string;
  subtitle: string;
}

const CategoryPageHeader: FunctionComponent<ICategoryPageHeader> = ({ title, subtitle }) => {
  return (
    <div className="category-page-header-wrapper">
      <div className="category-page-title">{title}</div>
      <div className="category-page-subtitle">{subtitle}</div>
    </div>
  );
}

export default CategoryPageHeader;
